// loadComponents.js - –∏—Å–ø—Ä–∞–≤–ª–µ–Ω–Ω–∞—è –≤–µ—Ä—Å–∏—è
async function loadComponent(elementId, componentPath) {
  try {
    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –±–∞–∑–æ–≤—ã–π –ø—É—Ç—å –¥–∏–Ω–∞–º–∏—á–µ—Å–∫–∏
    const basePath = window.location.origin + "/AQUANIKA/";
    const fullPath = `${basePath}${componentPath}`;

    const response = await fetch(fullPath);
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    const html = await response.text();
    const element = document.getElementById(elementId);
    if (element) {
      element.innerHTML = html;
    } else {
      console.warn(`–≠–ª–µ–º–µ–Ω—Ç —Å ID ${elementId} –Ω–µ –Ω–∞–π–¥–µ–Ω`);
    }
  } catch (error) {
    console.error(`–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ ${componentPath}:`, error);
  }
}

// –ó–∞–≥—Ä—É–∂–∞–µ–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
document.addEventListener("DOMContentLoaded", () => {
  loadComponent("header-placeholder", "components/partials/header.html");
  loadComponent("footer-placeholder", "components/partials/footer.html");
});
export { loadComponent };
